<template>
  <svg>
    <defs>
      <marker 
					id="arrow" 
					orient="auto"
					markerWidth="6" markerHeight="20"
					refX="3" refY="0">
        <path 
						d="M3,0 L3,10" 
						class="load-arrow"
						marker-end="url(#head)"
				/>
      </marker>
    </defs>

		<!-- Element path -->
    <path 
				:id="'path' + element.id" 
				:class="element.active ? 'path-element-active' : 'path-element'" 
				:d="elementPathDefinition"
		/>
		<!-- Distributed load -->
    <path 
				v-show="element.w !== 0 && view.elementLoads" 
				:id="'pathw' + element.id" 
				class="path-element-load" 
				:d="wPathDefinition"
		/>
		<!-- Moment diagram -->
		<path 
				v-show="view.momentDiagrams" 
				class="path-moment-diagram" 
				:d="momentDiagram"
		/>
		
		
		<!-- Shear diagram -->
		<path 
				v-show="view.shearDiagrams" 
				class="path-moment-diagram" 
				:d="shearDiagram"
		/>
		
		<!-- End releases -->
    <circle 
				class="circle-release" 
				v-show="!element.startRigid" 
				:cx="xStartOffset" 
				:cy="yStartOffset" 
				r="2"
		/>
    <circle 
				class="circle-release" 
				v-show="!element.endRigid" 
				:cx="xEndOffset" 
				:cy="yEndOffset" 
				r="2"
		/>

		<!-- Axial force -->
    <text 
				class="select-all"
				:transform="transformList" 
				v-show="view.elementForces"
		>
      <textPath 
					:href="'#path' + element.id" 
					startOffset="50%" 
					text-anchor="middle"
			>
        <tspan dy="-.25em">{{ forceRounded }}</tspan>
      </textPath>
    </text>

		<!-- Distributed load -->
    <text 
				class="select-all"
				v-show="element.w !== 0 && view.elementLoads">
      <textPath 
					:href="'#pathw' + element.id" 
					startOffset="50%" 
					text-anchor="middle"
			>
        <tspan class="load-text" dy="-0.25em">{{ Math.abs(wRounded) }}</tspan>
      </textPath>
    </text>

		<!-- Moment diagram values -->
		<circle 
				class="circle-diagram" 
				v-show="view.momentDiagrams" 
				:cx="momentDiagramStartX" 
				:cy="momentDiagramStartY" 
				:r="showStartMoment ? 8 : 4"
				@mouseenter="showStartMoment = true"
				@mouseleave="showStartMoment = false"
		></circle>
		<circle 
				class="circle-diagram" 
				v-show="view.momentDiagrams" 
				:cx="momentDiagramEndX" 
				:cy="momentDiagramEndY" 
				:r="showEndMoment ? 8 : 4"
				@mouseenter="showEndMoment = true"
				@mouseleave="showEndMoment = false"
		></circle>
		<text 
				v-show="view.momentDiagrams && (view.diagramValues || showStartMoment)" 
				:x="momentDiagramStartX" 
				:y="momentDiagramStartY" 
				dx="4"
				dy="-8"
		>{{ momentStartRounded }}
		</text>
		<text 
				v-show="view.momentDiagrams && (view.diagramValues || showEndMoment)" 
				:x="momentDiagramEndX" 
				:y="momentDiagramEndY" 
				dx="4"
				dy="-8"
		>{{ momentEndRounded }}
		</text>

		<!-- Shear diagram values -->
		<circle 
				class="circle-diagram" 
				v-show="view.shearDiagrams" 
				:cx="shearDiagramStartX" 
				:cy="shearDiagramStartY" 
				:r="showStartShear ? 8 : 4"
				@mouseenter="showStartShear = true"
				@mouseleave="showStartShear = false"
		></circle>
		<circle 
				class="circle-diagram" 
				v-show="view.shearDiagrams" 
				:cx="shearDiagramEndX" 
				:cy="shearDiagramEndY" 
				:r="showEndShear ? 8 : 4"
				@mouseenter="showEndShear = true"
				@mouseleave="showEndShear = false"
		></circle>
		<text 
				v-show="view.shearDiagrams && (view.diagramValues || showStartShear)" 
				:x="shearDiagramStartX" 
				:y="shearDiagramStartY" 
				dx="4"
				dy="-8"
		>{{ shearStartRounded }}
		</text>
		<text 
				v-show="view.shearDiagrams && (view.diagramValues || showEndShear)" 
				:x="shearDiagramEndX" 
				:y="shearDiagramEndY" 
				dx="4"
				dy="-8"
		>{{ shearEndRounded }}
		</text>
  </svg>
</template>

<script>
import math from 'mathjs'
// import 'gsap/BezierPlugin'

export default {
  name: 'ElementPath',
	props: ['element', 'end-forces', 'element-force', 'displacements', 'view', 'moment-scale', 'shear-scale', 'transform', 'is-active'],
	data() {
		return {
			showStartMoment: false,
			showEndMoment: false,
			showStartShear: false,
			showEndShear: false,
		}
	},
	computed: {

		/* Element properties */

		displacementsLocal () {
			return math.multiply(this.displacements, math.inv(this.transform));
		},
		l () {
			return Math.sqrt(Math.pow(this.element.end.x - this.element.start.x, 2) + 
					Math.pow(this.element.end.y - this.element.start.y, 2));
		},
		lDisplaced () {
			return (this.l - math.number(this.displacementsLocal[0]) + math.number(this.displacementsLocal[3]));
		},
		xStart () {
			return this.view.x + this.view.scale * this.element.start.x;
		},
		yStart () {
			return this.view.y - this.view.scale * this.element.start.y;
		},
		xEnd () {
			return this.view.x + this.view.scale * this.element.end.x;
		},
		yEnd () {
			return this.view.y - this.view.scale * this.element.end.y;
		},
		xMid () {
			return (this.xStart + this.xEnd) / 2;
		},
		yMid () {
			return (this.yStart + this.yEnd) / 2;
		},
		xPerp () {
			return (-this.yStart + this.yEnd) / (this.l * this.view.scale);
		},
		yPerp () {
			return (-this.xEnd + this.xStart) / (this.l * this.view.scale);
		},
		elementPathDefinition() {
			return 'M' + (this.xStart) + ',' + (this.yStart) + 'L' + (this.xEnd) + ',' + (this.yEnd);
		},

		/* Diagrams */

		wPathDefinition() {
			let str = 'm' + 12 * this.xPerp * Math.sign(-this.element.w) + ',' + 12 * this.yPerp * Math.sign(-this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(-this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(-this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(-this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(-this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(-this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(-this.element.w);
			return (Math.sign(this.element.w) < 0) ? 'M' + (this.xStart) + ',' + (this.yStart) + str : 'M' + (this.xEnd) + ',' + (this.yEnd) + str;
		},
		shearDiagramStartX() {
			let x = this.xStart - this.endForces[1] * this.shearScale * this.view.diagramScale * this.xPerp;
			return isNaN(x) ? 0 : x;
		},
		shearDiagramStartY() {
			let y = this.yStart - this.endForces[1] * this.shearScale * this.view.diagramScale * this.yPerp;
			return isNaN(y) ? 0 : y;
		},
		shearDiagramEndX() {
			let x = this.xEnd + this.endForces[4] * this.shearScale * this.view.diagramScale * this.xPerp;
			return isNaN(x) ? 0 : x;
		},
		shearDiagramEndY() {
			let y = this.yEnd + this.endForces[4] * this.shearScale * this.view.diagramScale * this.yPerp;
			return isNaN(y) ? 0 : y;
		},
		shearDiagram() {
			return 'M' + this.shearDiagramStartX + ',' + this.shearDiagramStartY
				+ 'L' + this.shearDiagramEndX + ',' + this.shearDiagramEndY;
		},
		momentDiagramStartX() {
			let x = this.xStart - this.endForces[2] * this.momentScale * this.view.diagramScale * this.xPerp;
			return isNaN(x) ? 0 : x;
		},
		momentDiagramStartY() {
			let y = this.yStart - this.endForces[2] * this.momentScale * this.view.diagramScale * this.yPerp;
			return isNaN(y) ? 0 : y;
		},
		momentDiagramEndX() {
			let x = this.xEnd + (0.5 * this.element.w * this.l * this.l + this.endForces[1] * this.l 
					- this.endForces[2]) * this.momentScale * this.view.diagramScale * this.xPerp;
			return isNaN(x) ? 0 : x;
		},
		momentDiagramEndY() {
			let y = this.yEnd + (0.5 * this.element.w * this.l * this.l + this.endForces[1] * this.l 
					- this.endForces[2]) * this.momentScale * this.view.diagramScale * this.yPerp;
			return isNaN(y) ? 0 : y;
		},
		momentDiagram() {
			return 'M' 
				+ this.momentDiagramStartX + ',' + this.momentDiagramStartY
				+ 'Q' 
				+ (this.xMid + (0.5 * this.endForces[1] * this.l - this.endForces[2]) * this.momentScale * this.view.diagramScale * this.xPerp) + ',' 
				+ (this.yMid + (0.5 * this.endForces[1] * this.l - this.endForces[2]) * this.momentScale * this.view.diagramScale * this.yPerp) + ' '
				+ this.momentDiagramEndX + ',' + this.momentDiagramEndY;
		},
		/* displacedShapeDiagram() {
			let data = BezierPlugin.bezierThrough(this.displacedControlPoints, 1);
			let d = 'M' + data.x[0].a + ',' + data.y[0].a + ' C' + this.segmentToString(data.x[0], data.y[0]);
			for (var i = 1; i < data.x.length; i++) {
				d += " " + this.segmentToString(data.x[i], data.y[i]);
			}
			return d;
		},
		displacedControlPoints () {
			let w = this.element.w;
			let l = this.l;
			let e = this.element.e;
			let iz = this.element.iz;
			let v0 = this.endForces[1];
			let m0 = math.number(this.endForces[2]);
			let c0 = math.number(this.displacementsLocal[2]);
			let d0 = math.number(this.displacementsLocal[1]);
			let point1 = d0;
			let point2 = d0 + 0.25 * c0 * l 	- (w * math.pow(l, 4) / 24 	- v0 * math.pow(l, 3) / 24 	+ m0 * math.pow(l, 2) / 12) / (e * iz);
			let point3 = d0	+ 0.50 * c0 * l 	- (w * math.pow(l, 4) / 12 	- v0 * math.pow(l, 3) / 12 	+ m0 * math.pow(l, 2) / 6) 	/ (e * iz);
			let point4 = d0 + 0.75 * c0 * l 	- (w * math.pow(l, 4) / 6 	- v0 * math.pow(l, 3) / 6 	+ m0 * math.pow(l, 2) / 3) 	/ (e * iz);
			let point5 = d0 + c0 * l 					- (w * math.pow(l, 4) / 3 	- v0 * math.pow(l, 3) / 3 	+ 7 * m0 * math.pow(l, 2) / 12) / (e * iz);
			return [
				{ 
					x: this.xStart + point1 * this.view.diagramScale * this.xPerp,
					y: this.yStart + point1 * this.view.diagramScale * this.yPerp
				},
				{ 
					x: (3 * this.xStart + this.xEnd) / 4 + (point2) * this.view.diagramScale * this.xPerp, 
					y: (3 * this.yStart + this.yEnd) / 4 + (point2) * this.view.diagramScale * this.yPerp
				},
				{ 
					x: (this.xStart + this.xEnd) / 2 		+ (point3) * this.view.diagramScale * this.xPerp, 
					y: (this.yStart + this.yEnd) / 2 		+ (point3) * this.view.diagramScale * this.yPerp
				},
				{ 
					x: (this.xStart + 3 * this.xEnd) / 4 + (point4) * this.view.diagramScale * this.xPerp, 
					y: (this.yStart + 3 * this.yEnd) / 4 + (point4) * this.view.diagramScale * this.yPerp
				},
				{ 
					x: this.xEnd + point5 * this.view.diagramScale * this.xPerp,
					y: this.yEnd + point5 * this.view.diagramScale * this.yPerp
				},
			];
		},
		controlPointDiagram () {
			let pts = this.displacedControlPoints;
			let d = 'M' + pts[0].x + ',' + pts[0].y;
			for (let i = 1; i < pts.length; i++) {
				d += " L" + pts[i].x + ',' + pts[i].y;
			}
			return d;
		}, */

		/* Displayed values */

		forceRounded () {
			return (math.equal(this.elementForce, 0)) ? 0 
					: this.elementForce.toPrecision(this.view.significantFigures);
		},	
		wRounded () {
			return (math.equal(this.element.w, 0)) ? 0 
					: this.element.w.toPrecision(this.view.significantFigures);
		},
		shearStartRounded() {
			return (math.equal(this.endForces[1], 0)) ? 0 
					: (-this.endForces[1]).toPrecision(this.view.significantFigures);
		},
		shearEndRounded() {
			return (math.equal(this.endForces[4], 0)) ? 0 
					: this.endForces[4].toPrecision(this.view.significantFigures);
		},
		momentStartRounded() {
			return (math.equal(this.endForces[2], 0)) ? 0 
					: (-this.endForces[2]).toPrecision(this.view.significantFigures);
		},
		momentEndRounded() {
			return (math.equal(this.endForces[5], 0)) ? 0 
					: this.endForces[5].toPrecision(this.view.significantFigures);
		},
	
		/* End releases */

		xStartOffset () {
			return this.xStart + (this.xEnd - this.xStart) * 0.125;
		},
		yStartOffset () {
			return this.yStart + (this.yEnd - this.yStart) * 0.125;
		},
		xEndOffset () {
			return this.xStart + (this.xEnd - this.xStart) * 0.875;
		},
		yEndOffset () {
			return this.yStart + (this.yEnd - this.yStart) * 0.875;
		},

		/* Auto-rotate */

		topRotate () {
			return (this.element.start.x < this.element.end.x) ? 0 : 180;
		},
		transformList () {
			return "rotate(" + this.topRotate + ' ' + this.xMid + ' ' + this.yMid + ')';
		},
	},
	methods: {
		/* segmentToString(x, y) {
  		return [x.b.toFixed(2), y.b.toFixed(2), x.c.toFixed(2), y.c.toFixed(2), x.d.toFixed(2), y.d.toFixed(2)].join(",");
		}, */
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "@/assets/styles/_variables.scss";

.path-element {
	stroke: $dark-warm;
	stroke-width: 2px; 
}
.path-element-active {
	stroke: purple;
	stroke-width: 4px; 
}
.path-element-load {
	marker-end: url(#arrow);
	marker-mid: url(#arrow);
	stroke: $loads-of-gray; 
	stroke-width: 1px; 
}
.path-moment-diagram {
	fill: none;
	stroke: $loads-of-gray;
}
.circle-release {
	fill: #fff;
	stroke: $dark-warm;
}
.circle-diagram {
	fill: $loads-of-gray; 
}
.load-arrow {
	stroke: $loads-of-gray; 
	fill: none;
}
.load-text {
	fill: $loads-of-gray; 
}
</style>



